require('regenerator-runtime/runtime');

const wire = require('./wire');
const constants = require('./constants');
const shortcuts = require('./shortcuts');

const libraries = {
    lib: require('./lib'),
    gateways: require('./gateways'),
    stores: require('./stores'),
    services: require('./services'),
    styles: require('./styles'),
    components: require('./components'),
    views: require('./views')
};

const wiring = {
    lib: 'random',
    gateways: 'fetch constants',
    stores: 'lib',
    services: 'fetch constants lib gateways stores URL',
    styles: 'lib stores services',
    components: 'lib',
    views: 'constants lib stores services components styles context'
};

const defaults = {
    name: 'Untitled',
    fetch: window.fetch,
    URL: window.URL,
    random: Math.random
};

const createInstance = args => {
    const { name, fetch, URL, random } = { ...defaults, ...args };
    const instance = wire(libraries, wiring, { context, constants, fetch, URL, random });
    instance.services.state.initialise();
    context.instances.push({ name, ...instance });
    return instance;
};

const context = {
    createInstance,
    instances: []
};

shortcuts(context);
module.exports = window.agileavatars = context;
