module.exports = ({ services, components, views }) => () => {

    const $container = components.dropzone(services.tags.insertImageFile);
    $container.className = 'avatars dropzone';

    const $controlPanel = document.createElement('div');
    $controlPanel.className = 'control-panel';

    $controlPanel.append(
        views.roleList(),
        views.imageUploadOptions(),
        views.briefUsageNotice(),
        views.optionsBar(),
        views.multipleAvatarsNotice()
    );

    $container.append(
        $controlPanel,
        views.tagList()
    );

    return $container;

};
