module.exports = context => {

    document.onkeyup = e => {

        if (e.ctrlKey && e.key === 'x') {
            context.agileavatars.filter(app => {
                return app.instanceName === 'main'
            }).forEach(app => {
                const { xray } = app.stores.options.getState();
                app.stores.options.setState({ xray: !xray });
            })
        }
    };

};
